@import url('https://fonts.googleapis.com/css?family=Playfair+Display:400,700,900&display=swap');
@import url('https://fonts.googleapis.com/css?family=Open+Sans:400,700&display=swap');

$break-large: 900px;

* {
  text-decoration: none;
}

.book-outer {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: space-around;
  overflow: hidden;
  height: 20rem;
  width: 15rem;
  padding: 10px;
  background-color: #B09398;
  transform: translateY(0);
  box-shadow: 0 3px 3px rgba(0, 0, 0, 0.3);
  margin: 1rem;
  cursor: pointer;
  transition: transform 0.25s, box-shadow 0.25s;

  &.create {
    background-color: #ccc;
  }

  .book-header {
    margin-top: -1rem;
  }

  .book-single {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
    height: 100%;

    h1 {
      text-align: center;
      width: 100%;
      margin: 0;
    }

    .create-book {
      height: 5rem;
      width: 5rem;
      margin: 0;
      opacity: 0.5;
    }
  }

  * {
    color: #fff;
    margin: 0;
  }

  &:hover {
    transform: translateY(-5px);
    box-shadow: 0 7px 7px rgba(0, 0, 0, 0.2);
  }

  h1 {
    font-family: 'Playfair Display', Times, serif;
    font-weight: 900;
    height: auto;
    margin: 1rem 0;
  }
  
  .outer {
    padding: 2vw 10vw;
  }
}

.custom-outer {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  padding: 0.25rem 0;

  .custom-icon {
    height: 0.8rem;
    width: 0.8rem;
    margin-right: 0.25rem;
    color: #fff;
  }

  p {
    font-weight: 900;
  }
}


.online-outer {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  padding: 0.25rem 0;

  .online {
    height: 0.8rem;
    width: 0.8rem;
    margin-right: 0.5rem;
    border-radius: 100%;
    background-color: #CDDFD8;
    animation: blink 1.5s infinite;
  }

  .online-text {
    font-weight: 900;
    color: #CDDFD8;
  }
}

@keyframes blink {
  0% {
    opacity: 0
  }

  50% {
    opacity: 1
  }

  100% {
    opacity: 0
  }
}